* {
  margin: 0;
  padding: 0;
}

:root {
  --bg-secundary: #ffc43a;
  --bg-primary: #fafafa;
}

body {
  background: #fafafa;
  background: var(--bg-primary);
}

header {
  width: 100%;
  height: 90px;
  background-color: #ffc33c;
  border-radius: 0 0 2em 2em;
}

header .header-search {
  position: relative;
  display: flex;
  justify-content: center;
  top: 37px;
}

main {
  margin-top: 25px;
}

main .main-trend {
  margin-left: 20px;
}

main .main-hot-recipes {
  margin-left: 20px;
}

:root {
  --bgColor: #ffffff;
}

input,
button {
  border: none;
  outline: none;
}

input {
  width: 80%;
  max-width: 13em;
  height: 1em;
  padding: 0.6em 2.6em 0.6em 1em;
  background-color: #ffffff;
  background-color: var(--bgColor);
  font-family: "Poppins", sans-serif;
  color: #282828;
  font-size: 1em;
  font-weight: 400;
  border: none;
  outline: none;
  border-radius: 15px;
  background-repeat: no-repeat;
  background-position-y: center;
  background-position-x: 95%;
}

.Food-Card {
    position: relative;
    margin-right: 20px;
}

.Food-Card .Food-Card--img {
    width: 300px;
    height: 200px;
    object-fit: cover;
    border-radius: 10px;
}

.Food-Card .Food-Card--title {
    position: absolute;
    top: 120px;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    -webkit-backdrop-filter: blur(20px);
            backdrop-filter: blur(20px);
    background-color: rgba(255, 255, 255, 0.8);
    width: 200px;
    height: 60px;
    padding: 17px;
    padding-right: 20px;
    padding-left: 20px;
    border-radius: 15px;
    box-shadow: 0 5px 15px rgba(50, 50, 50, 0.1);
}

.Food-Card .Food-Card--title h2 {
    font-family: 'Poppins', sans-serif; 
    color: #282828;
    font-size: 18px;
    font-weight: 500;
}
:root {
    --font: 'Poppins', sans-serif;
}

h1 {
    font-family: 'Poppins', sans-serif;
    font-family: var(--font); 
    color: #282828;
    font-size: 21px;
}

.main-trend--trending-foods {
    position: relative;
    display: flex;
    width: 330px;
    height: 230px;
    background-color: inherit;
    overflow: hidden;
    overflow: auto;
}

::-webkit-scrollbar {
    display: none;
}
.icon {
  padding: 0.6em 1em;
}

.bottomnav {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100vw;
  box-shadow: -3px 0 10px rgb(0 0 0 / 20%);
}

.bottomnav > ul {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.bottomnav > ul > li {
  list-style: none;
}

:root {
    --font: 'Poppins', sans-serif;
}

.main-hot-recipes--header {
    display: flex;
    justify-content: space-between;
}

.main-hot-recipes--header h1 {
    font-family: 'Poppins', sans-serif;
    font-family: var(--font);
    color: #282828;
    font-size: 21px;
}

.main-hot-recipes--header .link {
    display: flex;
    text-decoration: none;
}

.main-hot-recipes--header .link p {
    font-family: 'Poppins', sans-serif;
    font-family: var(--font);
    color: #ffc33c;
    font-size: 17px;
    margin-top: 18px;
    margin-right: -30px;
}

.main-hot-recipes--header .link .icon {
    margin-top: -2px;
}

